import * as React from "react"
import { graphql, PageProps } from "gatsby"
import TranslateLink from "../../components/translate-link";

interface ArticleTemplateProps extends PageProps {
  data: {
    markdownRemark: {
      frontmatter: {
        title: string
        date: string
        slug: string
        lang: string;
      }
      html: string
    }
  }
}

const ArticleTemplate: React.FC<ArticleTemplateProps> = ({ data }) => {
  const { markdownRemark } = data
  const { frontmatter, html } = markdownRemark

  const navigatorLanguage = navigator && navigator.language ? navigator.language : 'en'; // Example: "en-US"
  const domain = '1241am';
  const path = `articles${frontmatter.slug}`;
  const to = navigatorLanguage.substr(0, 2);
  const from = frontmatter.lang.substr(0, 2);

  return (
    <div>
      <div>
        <h1>{frontmatter.title}</h1>
        <h4>Wrote by AI </h4>
        <button  onClick={()=> alert('Beta version')}>Report</button>  
        <TranslateLink
          toLanguage={to}
          linkDomain={domain}
          linkPath={path}
          fromLanguage={from}
        />
      
        <h5>{frontmatter.date}</h5>
        <div dangerouslySetInnerHTML={{ __html: html }} />
      </div>
    </div>
  )
}

export const pageQuery = graphql`
  query($id: String!) {
    markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        slug
        title
        lang
      }
    }
  }
`

export default ArticleTemplate
