import React from 'react';

interface TranslateLinkProps {
  toLanguage: string;
  linkDomain: string;
  linkPath: string;
  fromLanguage: string;
}

const TranslateLink: React.FC<TranslateLinkProps> = ({
  toLanguage,
  linkDomain,
  linkPath,
  fromLanguage,
}) => {
  const handleClick = () => {
    const url = `https://${linkDomain}-com.translate.goog/${linkPath}/?_x_tr_sl=${fromLanguage}&_x_tr_tl=${toLanguage}&_x_tr_hl=${fromLanguage}&_x_tr_pto=wapp`;
    window.open(url, '_self');
  };
  const message = ({en: "Translate to Engish", es: "Traduce a Espa√±ol"})[toLanguage]
  if (toLanguage === fromLanguage) {
    return null;
  }
  return (
    <button onClick={handleClick}>{message}</button>
  );
};

export default TranslateLink;
